//
// Load core variables and mixins
// --------------------------------------------------

@import "variables.less";
@import "mixins.less";


.btn-shadow(@color) {
  #gradient > .vertical-three-colors(lighten(@color, 8%), @color, 60%, darken(@color, 4%));
  filter: none;
  //border-bottom: 1px solid darken(@color, 10%);
}

// Navbar =====================================================================

.navbar {
  .btn-shadow(@navbar-default-bg);
  filter: none;
  .box-shadow(0 1px 10px rgba(0, 0, 0, 0.1));
  font-size: 14px;
  text-transform: uppercase;
  min-height: 0px;

  &-default {

    .badge {
      background-color: darken(@brand-primary, 50%);
      color: @navbar-default-bg;
    }
  }
  
  &-inverse {
    #gradient > .vertical-three-colors(lighten(@navbar-inverse-bg, 8%), lighten(@navbar-inverse-bg, 4%), 60%, darken(@navbar-inverse-bg, 2%));
    filter: none;
    border-bottom: 1px solid darken(@navbar-inverse-bg, 10%);

    .badge {
      background-color: #fff;
      color: @navbar-inverse-bg;
    }
  }

  .navbar-nav > li > a,
  .navbar-nav .dropdown-menu>li>a
	{
	font-weight: bold;
	color: #666;
}

  .navbar-nav > li > a,
  &-brand {
    text-shadow: 0 1px 0 rgba(0, 0, 0, 0.1);
  }
}

.navbar-toggle {
  background-color: #fff;
  margin-right: 5px;
  margin-top: 37px;
  height: 25px;
  .icon-bar {
    background-color: @brand-primary;
    }
  }

@media only screen and (max-width: 991px) {

	.nav>li>a
	{
		padding-right: 15px;
		padding-left: 15px;
	}
}
  
@media only screen and (max-width: 850px) {

	.nav>li>a
	{
		padding-right: 11px;
		padding-left: 11px;
	}
}

@media (max-width: @grid-float-breakpoint-max) {

  .navbar {

    .dropdown-header {
      color: #fff;
    }

    .dropdown-menu {
      a {
        color: #fff;
      }
    }
  
}
}

  //get horizontal scrollbar without this - not sure if this is a bug I've introduced
  //TODO diff variables.less
  .container>.navbar-header
  {
    margin-right: 0;
    margin-left:  0;
  }



// Buttons ====================================================================

.btn {

  text-shadow: 0 1px 0 rgba(0, 0, 0, 0.1);

  .caret {
    border-top-color: #fff;
  }
}

.btn-default {
  
  .btn-shadow(@btn-default-bg);

  &:hover {
    color: @btn-default-color;
  }

  .caret {
    border-top-color: @text-color;
  }
}

.btn-default {
  .btn-shadow(@btn-default-bg);
}

.btn-primary {
  .btn-shadow(@btn-primary-bg);
}

.btn-success {
  .btn-shadow(@btn-success-bg);
}

.btn-info {
  .btn-shadow(@btn-info-bg);
}

.btn-warning {
  .btn-shadow(@btn-warning-bg);
}

.btn-danger {
  .btn-shadow(@btn-danger-bg);
}

